'use client';

import { CheckCircle } from 'lucide-react';

interface Plan {
  name: string;
  price: string;
  period?: string;
  devices: string;
  features: string[];
  ctaText: string;
  highlighted: boolean;
}

interface PricingSectionProps {
  plans: Plan[];
}

export default function PricingSection({ plans }: PricingSectionProps) {
  return (
    <section className="py-20 bg-white">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold text-center mb-16 text-black">
          Tarification Flexible
        </h2>
        
        <div className="grid md:grid-cols-3 gap-8 max-w-5xl mx-auto">
          {plans.map((plan, index) => (
            <div 
              key={index} 
              className={`p-8 rounded-lg border transition ${
                plan.highlighted 
                  ? 'bg-[#e38f00] text-white border-[#e38f00] shadow-2xl scale-105' 
                  : 'bg-white border-gray-200 shadow-lg hover:shadow-xl'
              }`}
            >
              <h3 className="text-2xl font-bold mb-2">{plan.name}</h3>
              <p className={plan.highlighted ? 'text-white/90' : 'text-gray-700'}>
                {plan.devices}
              </p>
              
              <div className="my-6">
                <span className="text-4xl font-bold">{plan.price}</span>
                {plan.price !== 'Contactez-nous' && (
                  <span className={plan.highlighted ? 'text-white/90 ml-2' : 'text-gray-700 ml-2'}>
                    /{plan.period}
                  </span>
                )}
              </div>
              
              <ul className="space-y-3 mb-8">
                {plan.features.map((feature, i) => (
                  <li key={i} className="flex items-center gap-2">
                    <CheckCircle className={`w-5 h-5 ${plan.highlighted ? 'text-white' : 'text-[#e38f00]'}`} />
                    <span className={plan.highlighted ? 'text-white/90' : 'text-gray-700'}>
                      {feature}
                    </span>
                  </li>
                ))}
              </ul>
              
              <button className={`w-full py-3 rounded-lg font-semibold transition ${
                plan.highlighted
                  ? 'bg-white text-[#e38f00] hover:bg-gray-100'
                  : 'bg-[#e38f00] text-white hover:bg-[#d48500]'
              }`}>
                {plan.ctaText}
              </button>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}