'use client';

interface TechStackProps {
  technologies: string[];
}

export default function TechStack({ technologies }: TechStackProps) {
  return (
    <section className="py-20 bg-white">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold text-center mb-16 text-black">
          Technologies Utilis√©es
        </h2>
        
        <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5 gap-6 max-w-4xl mx-auto">
          {technologies.map((tech, index) => (
            <div 
              key={index}
              className="bg-white p-6 rounded-lg border border-gray-200 hover:shadow-lg transition text-center"
            >
              <p className="font-semibold text-black">{tech}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}