'use client';

interface UseCase {
  title: string;
  description: string;
  icon: string;
}

interface UseCaseCarouselProps {
  useCases: UseCase[];
}

export default function UseCaseCarousel({ useCases }: UseCaseCarouselProps) {
  return (
    <section className="py-20">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold text-center mb-16 text-black">
          Cas d'Utilisation
        </h2>
        
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {useCases.map((useCase, index) => (
            <div key={index} className="bg-white p-8 rounded-lg border border-gray-200 hover:shadow-xl transition transform hover:-translate-y-2 hover:border-[#e38f00]">
              <div className="text-5xl mb-4">{useCase.icon}</div>
              <h3 className="text-xl font-bold mb-3 text-black">{useCase.title}</h3>
              <p className="text-gray-700">{useCase.description}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}