'use client';

interface ServiceNavProps {
  sections: string[];
  activeSection: string;
  onSectionChange: (section: string) => void;
}

export default function ServiceNav({ sections, activeSection, onSectionChange }: ServiceNavProps) {
  return (
    <nav className="sticky top-20 bg-white/95 backdrop-blur-xl border-b border-gray-200 z-40">
      <div className="container mx-auto px-4">
        <div className="flex overflow-x-auto gap-4 py-4">
          {sections.map((section) => (
            <button
              key={section}
              onClick={() => onSectionChange(section)}
              className={`px-4 py-2 font-semibold rounded-lg transition whitespace-nowrap ${
                activeSection === section
                  ? 'bg-[#e38f00] text-white'
                  : 'text-gray-700 hover:bg-[#e38f00]/10'
              }`}
            >
              {section.charAt(0).toUpperCase() + section.slice(1)}
            </button>
          ))}
        </div>
      </div>
    </nav>
  );
}